import { randomInt } from 'crypto';
import React, { useState, useEffect, useRef } from 'react';

interface AnimatedTextProps {
    text: string;
    onComplete: () => void;
    onHeightChange: (height: number) => void;
    args: any;
}

const AnimatedText: React.FC<AnimatedTextProps> = ({ text, onComplete, onHeightChange, args }) => {
  const [visibleText, setVisibleText] = useState('');
  const textRef = useRef<HTMLDivElement>(null);
  const [completed, setCompleted] = useState(false);

  useEffect(() => {
    if (completed) return;

    let index = 0;

    const interval = setInterval(() => {
      if (index < text.length) {
        setVisibleText((prevText) => prevText + text[index]);

        if (textRef.current) {
          const { height } = textRef.current.getBoundingClientRect();
          onHeightChange(height);
        }

        index++;
      } else {
        clearInterval(interval);
        onComplete();
        setCompleted(true);
      }
    }, 10); // Adjust the typing speed here

    return () => clearInterval(interval);
  }, [text, onComplete, onHeightChange, completed]);

  return (
    <div
      style={{
        background: args.theme.secondaryBackgroundColor,
        border: '1px solid transparent',
        borderRadius: '10px',
        padding: '10px 14px',
        margin: '5px 20px',
        maxWidth: '80%',
        fontSize: '1rem',
        flex: 1,
        display: 'flex',
      }}
      ref={textRef}
    >
      {visibleText}
    </div>
  );
};

export default AnimatedText;
