import {
  // eslint-disable-next-line 
  Streamlit,
  StreamlitComponentBase,
  withStreamlitConnection,
} from "streamlit-component-lib"
import React, { ReactNode } from "react"
import styled from "@emotion/styled";
import ChatContainer from './chatContainer'

class Chat extends StreamlitComponentBase {

  public render = (): ReactNode => {
    Streamlit.setFrameHeight(window.innerHeight)
    const { isUser, message, animated, key } = this.props.args;
    // consrole.log(this.props.args)
    // Streamlit sends us a theme object via props that we can use to ensure
    // that our component has visuals that match the active theme in a
    // streamlit app.
    const { theme } = this.props
    
    // Maintain compatibility with older versions of Streamlit that don't send
    // a theme object.
    if (!theme) {
      return <div>Theme is undefined, please check streamlit version.</div>
    }
    // Styles for the Typing component
  const StyledChatContainer = styled(ChatContainer)({
  });
    return (
        <StyledChatContainer args={{ isUser: isUser, message: message, theme: theme, animated: animated, key: key}}/>

    )
  }
}

export default withStreamlitConnection(Chat);
