import React, { useEffect, useRef, useState } from "react";
import styled from "@emotion/styled";
import { css } from "@emotion/react";
import AnimatedText from './AnimatedText'; // Assuming both files are in the same folder
import { Streamlit } from "streamlit-component-lib";

interface ChatProps {
  args: {
    isUser: boolean;
    message: string;
    animated: boolean;
    key: any;
    theme: any;
  
  };
}

interface AvatarProps {
  isUser: boolean;
}

interface ChatContainerProps {
  isUser: boolean;
}

const ChatContainer: React.FC<ChatProps> = ({ args }) => {
  const { isUser, message, animated, key } = args;
  const [isAnimationCompleted, setAnimationCompleted] = useState(false);
  const containerHeightRef = useRef(0);

  // Streamlit.setComponentValue(args); // Send the value back to Streamlit
 
  


  let avatarUrl = "";
  if (!isUser) {
    avatarUrl = `https://user-images.githubusercontent.com/6371329/234966848-c28e98de-221c-433d-bd33-eea5f04414fe.png`;
  } else {
    avatarUrl = `https://api.dicebear.com/6.x/shapes/svg?shape1Color=6941EB&shape2Color=00C8BC&shape3Color=ffa532&backgroundColor=ffffff&backgroundType=gradientLinear,solid`;
  }

  // Styles for the Typing component
  const TypingStyle = styled(AnimatedText)({
    background: args.theme.secondaryBackgroundColor,
  });

  // styles for the avatar image
  const Avatar = styled.img<AvatarProps>(
    {
      border: `1px solid transparent`,
      borderRadius: "50%",
      height: "5rem",
      width: "5rem",
      margin: 0,
    },
    (props: { isUser: boolean }) => {
      // specific styles
      if (props.isUser) {
        return css`
          height: 4rem;
          width: 4rem;
          margin: 0;
        `;
      }
      return css``;
    }
  );

  // styles for the message box
  const Message = styled.div({
    display: "inline-block",
    background: args.theme.secondaryBackgroundColor,
    border: "1px solid transparent",
    borderRadius: "10px",
    padding: "10px 14px",
    margin: "5px 20px",
    height: "auto",
    maxWidth: "70%",
    whiteSpace: "pre-line",
  });

  // styles for the container
  const Chat = styled.div<ChatContainerProps>(
    {
      display: "flex",
      flex: 1,
      maxWidth: "100vw",
      height: "auto",
      overflow: "auto",
    },
    (props: { isUser: boolean }) => {
      // specific styles
      if (props.isUser) {
        return css`
        flex-direction: row-reverse;
        & > div {/
          text-align: right;
        }
      `;
      }
      return css``;
    }
  );

  const handleAnimationComplete = () => {
    setAnimationCompleted(true);
    args.animated=true
    
  };

  const updateHeight = (height: number) => {
    containerHeightRef.current = height;
    //console.log(containerHeightRef.current)
    if (height < 80){
      Streamlit.setFrameHeight(80);
    }else{
      Streamlit.setFrameHeight(height);
    }
      

  };

  return (
    <div style={{ height: `${containerHeightRef.current}px` }}>
      <Chat isUser={isUser}>
        <Avatar isUser={isUser} src={avatarUrl} alt="profile" draggable="false" />
          <Message>{message}</Message>
      </Chat>
    </div>
  );
}

export default ChatContainer